
.input-group {
  input[type="text"], input[type="password"], input[type="number"] {
    width: 100%;
    font-size: 20px;
    margin-top: 18px;
    padding-left: 8px;
    border: none;
    border-bottom: 2px solid #eee;
    -webkit-transition: border .2s; /* Safari */
    transition: border .2s;
    color: transparent;
    + label {
      color: #777;
      position: relative;
      bottom: 32.5px;
      left: 5px;
      font-size: 20px;
      -webkit-transition: all .2s; /* Safari */
      transition: all .2s;
      height: 16px;
      display: block;
      cursor: text;
    }
    &::-webkit-input-placeholder {
      opacity: 0;
    }
    &::-moz-placeholder {
      opacity: 0;
    }
    &:-moz-placeholder {
      opacity: 0;
    }
    &:-ms-input-placeholder {
      opacity: 0;
    }
    &:focus {
      border-bottom: 2px solid $primary-color;
      outline: none;
      color: #777;
      &::-webkit-input-placeholder {
        opacity: 1;
      }
      &::-moz-placeholder {
        opacity: 1;
      }
      &:-moz-placeholder {
        opacity: 1;
      }
      &:-ms-input-placeholder {
        opacity: 1;
      }
    }
    &.is-not-empty {
      color: #212121;
      &:focus {
        color: #212121;
      }
    }
    &.is-not-empty + label {
      bottom: 50px;
      font-size: 16px;
      &:after {
        content: ":";
      }
    }
    &:focus + label {
      color: $primary-color;
      bottom: 50px;
      font-size: 16px;
      cursor: default;
      &:after {
        content: ":";
      }
    }
  }
}